.school {
  padding-bottom: 50px;

  &__container {
    padding: 40px;
    display: flex;
    justify-content: space-between;

    background-color: #fff;
    border-bottom-left-radius: 16px;
    border-bottom-right-radius: 16px;
  }

  &__img-wrapper {
    flex: 0 1 410px;
    margin-right: 20px;

    font-size: 0;

    border-radius: 4px;
    overflow: hidden;
  }

  &__text-wrapper {
    flex: 0 2 670px;
    display: flex;
    flex-direction: column;
  }

  &__desc {
    margin-bottom: 32px;
  }

  &__link {
    margin-top: auto;

    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    font-feature-settings: 'pnum' on, 'lnum' on;
  }
}

@media(max-width: 980px) {
  .school {
    &__container {
      padding: 18px 16px;
    }
  }
}

@media(max-width: 620px) {
  .school {
    &__container {
      flex-direction: column-reverse;
    }

    &__img-wrapper {
      flex: 0 1 auto;
      margin-right: 0;
    }

    &__text-wrapper {
      flex: 0 1 auto;
      margin-bottom: 16px;
    }
  }
}
