.truth {
  padding: var(--section-p);

  &__container {
    display: flex;
    align-items: center;
  }

  &__img-wrapper {
    margin-right: 85px;
    flex: 0 1 615px;

    border-radius: 16px;
    overflow: hidden;
  }

  &__text-wrapper {
    display: flex;
    flex-direction: column;
    flex: 0 1 466px;
  }

  &__desc {
    margin-bottom: 24px;
    max-width: 410px;
  }
}

.swiper-truth {
  padding: 16px;
  max-width: 466px;

  border-radius: 16px;
  background-color: var(--grey-700);

  &__wrapper {
    padding-bottom: 30px;
  }

  &__slide {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin: 0;
    margin-bottom: 24px;

    font-family: 'Inter';
    font-weight: 900;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    text-transform: uppercase;
    color: var(--light);
  }

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 8px;
    }
  }
}

.card-recall {
  padding: 8px;

  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: var(--grey-600);
  border-radius: 16px;

  &--yan {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 6px;
      right: 6px;

      width: 14px;
      height: 18px;
      display: block;
      background: url(../img/yan-icon.svg);
    }
  }

  &__rating{
    margin-bottom: 4px;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 900;
    font-size: 32px;
    line-height: 39px;
    text-transform: uppercase;
    color: var(--light);
  }

  &__list {
    margin-bottom: 8px;

    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 3px;
    }
  }

  &__icon {
    width: 12px;
    height: 12px;

    fill: #F2C94C;
  }

  &__number {
    padding: 4px 8px;

    font-family: 'Inter';
    font-weight: 400;
    font-size: 11px;
    line-height: 13px;
    color: var(--light);

    background-color: var(--grey-500);
    border-radius: 8px;
  }
}

@media (max-width: 1080px) {
  .truth {
    &__img-wrapper {
      margin-right: 40px;
    }
  }
}

@media (max-width: 980px) {
  .truth {
    &__text-wrapper {
      flex: 0 1 295px;
    }
  }

  .swiper-truth {
    padding: 0;
    padding-bottom: 30px;
    max-width: 295px;

    border-radius: 0;
    background-color: transparent;

    &__btn {
      top: auto;
      bottom: 10px;
    }

    &__pagination {
      bottom: 20px !important;
    }

    &__title {
      font-weight: 600;
      font-size: 16px;
      line-height: 19px;
    }
  }
}

@media (max-width: 740px) {
  .truth {
    &__container {
      flex-direction: column;
    }

    &__img-wrapper {
      margin-right: 0;
      flex: 0 1 auto;
      order: 2;
    }

    &__text-wrapper {
      width: 100%;
      text-align: center;
    }

    &__desc {
      max-width: none;
    }
  }
}

@media (max-width: 480px) {
  .truth {
    &__text-wrapper {
      text-align: start;
    }

    &__img-wrapper {
      margin-right: -15px;
      margin-left: -15px;
    }
  }
}
