.except {
  padding-top: 50px;

  &__container {
    padding: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    background-color: #fff;
    border-top-left-radius: 16px;
    border-top-right-radius: 16px;
  }

  &__text-wrapper {
    margin-right: 20px;
    display: flex;
    flex-direction: column;

    flex: 0 1 416px;
  }

  &__desc {
    margin-bottom: 32px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
  }

  &__item {
    margin-right: 20px;
    margin-bottom: 20px;
    padding: 10px;
    display: flex;
    align-items: center;

    font-weight: 500;
    font-size: 20.0373px;
    line-height: 24px;
    font-feature-settings: "pnum" on, "lnum" on;
    color: var(--grey-700);

    background-color: var(--grey-100);
    border-radius: 10px;

    &::before {
      content: '';
      margin-right: 5px;
      width: 8px;
      height: 8px;
      display: inline-block;

      background-color: var(--accient-blue);
      border-radius: 50%;
    }
  }

  &__img-list {
    flex: 0 2 670px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 16px;
  }

  &__img-item {
    font-size: 0;

    border-radius: 4px;
    overflow: hidden;

    &--big {
      grid-column: 3 span;
      border-radius: 16px;
    }
  }
}

@media(max-width: 980px) {
  .except {
    &__container {
      padding: 18px 16px;
    }

    &__item {
      margin-right: 8px;
      margin-bottom: 8px;
      padding: 8px;

      font-size: 15px;
      line-height: 18px;
    }
  }
}

@media(max-width: 620px) {
  .except {
    &__container {
      flex-direction: column;
    }

    &__text-wrapper {
      flex: 0 1 auto;
      margin-right: 0;
      margin-bottom: 16px;
      max-width: 310px;

      align-self: flex-start;
    }

    &__img-list {
      flex: 0 1 auto;
    }

    &__text-wrapper {
      flex: 0 1 auto;
    }
  }
}
