.simply {
  padding-bottom: 50px;

  &__wrapper {
    position: relative;
    z-index: 2;

    margin-top: -107px;
    padding: 50px;
    display: flex;

    background: #FFFFFF;
    border-radius: 16px;
  }

  &__text-wrapper {
    margin-right: 30px;
    flex: 0 1 420px;

    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__img-wrapper {
    font-size: 0;

    border-radius: 8px;
    overflow: hidden;
  }
}

@media (max-width: 980px) {
  .simply {
    padding-bottom: 20px;

    &__wrapper {
      margin-top: -16px;
      padding: 30px;
    }

    &__img-wrapper {
      max-width: 620px;
    }
  }
}

@media (max-width: 680px) {
  .simply {
    &__wrapper {
      margin-top: -16px;
      padding: 24px 16px;
      flex-direction: column;
    }

    &__text-wrapper {
      flex: 0 1 auto;
      margin-right: 0;
      margin-bottom: 36px;
    }

  }
}

@media (max-width: 480px) {
  .simply {
    &__wrapper {
      margin-right: -15px;
      margin-left: -15px;
    }
  }
}

@media (max-width: 420px) {
  .simply {
    &__text-wrapper {
      margin-bottom: 16px;
    }
  }
}
