.price {
  padding: var(--section-p);

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__desc {
    margin-bottom: 32px;
  }

  &__list {
    margin-bottom: 32px;
    width: 100%;
    overflow: visible;

    & .swiper-wrapper {
      align-items: stretch;
    }
  }

  &__settings {
    position: relative;

    & .swiper-pagination {
      bottom: -46px;
    }

    & .swiper-button-prev,
    & .swiper-button-next {
      top: 40px;
    }

    & .swiper-pagination,
    & .swiper-button-prev,
    & .swiper-button-next {
      display: none;
    }
  }

  &__btn {
    width: 100%;
  }
}

.card-price {
  padding: 19px 11px;
  max-width: 370px;
  display: flex;
  flex-direction: column;

  color: var(--light);

  background-color: var(--grey-600);
  border: 5px solid var(--accient-blue);
  border-radius: 16px;
  box-sizing: border-box;

  &--barber {
    border-color: var(--grey-600);
  }

  &--older-barber {
    border-color: var(--accient-blue);
  }

  &--chief-barber {
    border-color: var(--orange);
  }

  &__lvl {
    margin-bottom: 2px;

    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    color: var(--grey-200);
  }

  &__title {
    margin: 0;
    margin-bottom: 16px;

    font-family: 'Inter';
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    text-transform: uppercase;
  }

  &__user-list {
    margin-bottom: 16px;
    display: flex;
  }

  &__user-item {
    width: 50px;
    height: 50px;

    border: 2px solid white;
    border-radius: 50%;

    overflow: hidden;

    &:not(:first-child) {
      margin-left: -20px;
    }

    &--other {
      display: flex;
      justify-content: center;
      align-items: center;

      font-family: 'Inter';
      font-weight: 500;
      font-size: 20px;
      line-height: 24px;

      background-color: var(--grey-600);
    }

    &--chief-barber {
      border-color: var(--orange);
      margin-right: 8px;
    }
  }

  &__user-desc {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__user-name {
    margin-bottom: 4px;

    font-family: 'Inter';
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    text-transform: uppercase;
  }

  &__user-status {
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
  }

  &__feature-list {
    margin-bottom: 24px;
  }
}

.feature-list {

  &--barber {
    & .feature-list__item:nth-child(1) .feature-list__value::before {
      width: 71%;
    }
    & .feature-list__item:nth-child(2) .feature-list__value::before {
      width: 50%;
    }
    & .feature-list__item:nth-child(3) .feature-list__value::before {
      width: 43%;
    }
  }

  &--older-barber {
    & .feature-list__item:nth-child(1) .feature-list__value::before {
      width: 88%;
    }
    & .feature-list__item:nth-child(2) .feature-list__value::before {
      width: 60%;
    }
    & .feature-list__item:nth-child(3) .feature-list__value::before {
      width: 74%;
    }
  }

  &--chief-barber {
    & .feature-list__item:nth-child(1) .feature-list__value::before {
      width: 110%;
    }
    & .feature-list__item:nth-child(2) .feature-list__value::before {
      width: 110%;
    }
    & .feature-list__item:nth-child(3) .feature-list__value::before {
      width: 110%;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 8px;
    }
  }

  &__name {
    margin-bottom: 4px;

    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    color: var(--grey-200);
  }

  &__value {
    position: relative;

    width: 100%;
    height: 6px;

    background-color: var(--grey-700);
    border-radius: 5px;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;

      background-color: var(--accient-blue);
      border-radius: 5px;
    }
  }
}

.price-list {
  &__item {
    padding: 4px 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    font-family: 'Inter';
    font-size: 14px;
    line-height: 17px;

    background-color: var(--grey-500);
    border-radius: 8px;

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &__name {
    max-width: 165px;

    font-weight: 400;
  }

  &__value {
    font-weight: 500;
  }
}

@media(max-width: 1215px) {
  .price {

    &__list {
      margin-bottom: 72px;
    }

    &__settings {

      & .swiper-pagination {
        display: block;
      }

      & .swiper-button-prev,
      & .swiper-button-next {
        display: flex;
      }
    }

    &__btn {
      width: 100%;
    }
  }
}
