.news {
  padding: var(--section-p);

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__desc {
    margin-bottom: 32px;
  }

  &__list {
    margin-bottom: 32px;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;
  }

  &__link {
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    text-decoration-line: underline;
    color: var(--grey-200);

    transition: color .2s ease-in-out;

    &:hover {
      color: white;
    }
  }
}

.card-news {
  background-color: var(--grey-700);
  border-radius: 16px;
  overflow: hidden;

  &__img-wrapper {
    font-size: 0;

    border-radius: 8px;

    & img {
      width: 100%;
      object-fit: cover;
    }
  }

  &__content-wrapper {
    padding: 16px;
    display: flex;
    flex-direction: column;

    color: white;
  }

  &__title {
    margin: 0;
    margin-bottom: 8px;

    font-family: 'Inter';
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
  }

  &__desc {
    margin: 0;
    margin-bottom: 16px;

    font-weight: 400;
    font-size: 14px;
    line-height: 16px;

    display: -webkit-box;
    text-overflow: ellipsis;
    overflow: hidden;
    -o-text-overflow: ellipsis;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }

  &__bottom-wrapper {
    margin-top: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__time {
    padding: 8px;
    margin-right: 10px;
    display: flex;
    align-items: center;

    background-color: var(--grey-500);
    border-radius: 8px;

    &-icon {
      width: 18px;
      height: 18px;
      margin-right: 8px;
    }

    &-value {
      font-family: 'Inter';
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
    }
  }

  &__link {
    padding: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    color: inherit;

    &:hover {
      & .card-news__link-icon {
        transform: translateX(15px);
      }
    }

    &-text {
      margin-right: 12px;

      font-weight: 600;
      font-size: 16px;
      line-height: 19px;
      color: inherit;
    }

    &-icon {
      transition: transform .2s ease-in-out;
    }
  }
}

@media(max-width: 1130px) {
  .news {

    &__list {
      grid-template-columns: repeat(3, 1fr);
    }

    &__item:nth-child(4) {
      display: none;
    }
  }
}

@media(max-width: 900px) {
  .news {

    &__list {
      grid-template-columns: repeat(2, 1fr);
    }

    &__item:nth-child(3) {
      display: none;
    }
  }
}

@media(max-width: 620px) {
  .news {

    &__list {
      grid-template-columns: repeat(1, 1fr);
    }

    &__item:nth-child(2) {
      display: none;
    }
  }
}
