.profitable {
  padding: var(--section-p);

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }

  &__desc {
    margin-bottom: 24px;
  }

  &__img-wrapper {
    font-size: 0;
  }
}

@media(max-width: 640px) {
  .profitable {

    &__wrapper {
      display: flex;
      flex-direction: column;
    }

    &__col {
      display: flex;
      flex-direction: column;
    }

    &__img-wrapper {
      align-self: center;
    }
  }
}
